<template>
  <div class="upload-view">
    <VideoBar />
    <div class="upload-container">
      <!--  -->
      <DataNav currentPage="upload" />

      <!--  -->
      <div class="upload-main">
        <!--  -->
        <div
          class="upload-area"
          v-if="!selectedFile"
          @dragover.prevent
          @drop.prevent="handleDrop"
        >
          <div class="upload-box">
            <i class="bi bi-cloud-arrow-up"></i>
            <h3></h3>
            <p> mp4flvavimovwmvmpgmkv </p>
            <div class="upload-limits">
              <span><i class="bi bi-clock"></i> 60</span>
              <span><i class="bi bi-hdd"></i> 8GB</span>
            </div>
            <input
              type="file"
              ref="fileInput"
              @change="handleFileSelect"
              accept="video/*"
              class="file-input"
            />
            <button class="select-btn" @click="triggerFileInput"></button>
          </div>
        </div>

        <!--  -->
        <div class="video-form" v-else>
          <div class="form-header">
            <h2></h2>
            <button class="cancel-btn" @click="resetUpload">
              <i class="bi bi-x"></i>
              
            </button>
          </div>

          <div class="form-content">
            <!--  -->
            <div class="form-section">
              <h3></h3>
              <div class="form-group">
                <label> <span class="required">*</span></label>
                <input
                  type="text"
                  v-model="videoInfo.title"
                  placeholder=""
                  maxlength="80"
                />
                <span class="char-count">{{ videoInfo.title.length }}/80</span>
              </div>

              <div class="form-group">
                <label> <span class="required">*</span></label>
                <select v-model="videoInfo.category">
                  <option value=""></option>
                  <option value="animation"></option>
                  <option value="music"></option>
                  <option value="dance"></option>
                  <option value="game"></option>
                  <option value="knowledge"></option>
                  <option value="tech"></option>
                  <option value="sports"></option>
                  <option value="car"></option>
                  <option value="life"></option>
                  <option value="food"></option>
                </select>
              </div>

              <div class="form-group">
                <label></label>
                <textarea
                  v-model="videoInfo.description"
                  placeholder=""
                  rows="4"
                ></textarea>
              </div>

              <div class="form-group">
                <label></label>
                <div class="tags-input">
                  <div class="tag" v-for="(tag, index) in videoInfo.tags" :key="index">
                    {{ tag }}
                    <i class="bi bi-x" @click="removeTag(index)"></i>
                  </div>
                  <input
                    type="text"
                    v-model="tagInput"
                    @keydown.enter.prevent="addTag"
                    placeholder="10"
                    :disabled="videoInfo.tags.length >= 10"
                  />
                </div>
              </div>
            </div>

            <!--  -->
            <div class="form-section">
              <h3></h3>
              <div class="cover-upload">
                <div class="cover-preview" v-if="videoInfo.cover">
                  <img :src="videoInfo.cover" alt="" />
                  <div class="cover-actions">
                    <button @click="removeCover"><i class="bi bi-trash"></i></button>
                  </div>
                </div>
                <div class="cover-upload-box" v-else @click="triggerCoverInput">
                  <i class="bi bi-image"></i>
                  <p></p>
                  <input
                    type="file"
                    ref="coverInput"
                    @change="handleCoverSelect"
                    accept="image/*"
                    class="file-input"
                  />
                </div>
              </div>
            </div>

            <!--  -->
            <div class="form-section">
              <h3></h3>
              <div class="form-group">
                <label></label>
                <div class="radio-group">
                  <label class="radio">
                    <input type="radio" v-model="videoInfo.publishType" value="now" />
                    <span></span>
                  </label>
                  <label class="radio">
                    <input
                      type="radio"
                      v-model="videoInfo.publishType"
                      value="schedule"
                    />
                    <span></span>
                  </label>
                </div>
                <input
                  type="datetime-local"
                  v-if="videoInfo.publishType === 'schedule'"
                  v-model="videoInfo.scheduleTime"
                  class="schedule-time"
                />
              </div>
            </div>
          </div>

          <!--  -->
          <div class="form-actions" @click="submitVideo">
            <button class="draft-btn"></button>
            <button class="submit-btn"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import VideoBar from "@/components/navBar/VideoBar.vue";
import DataNav from "@/components/navBar/DataNav.vue";
import { videoInfos } from "@/data/videoInfos";

export default {
  name: "UpLoadView",
  components: {
    VideoBar,
    DataNav,
  },
  data() {
    return {
      selectedFile: null,
      videoUrl: null,
      tagInput: "",
      videoInfo: {
        title: "",
        category: "",
        description: "",
        tags: [],
        cover: null,
        publishType: "now",
        scheduleTime: null,
      },
    };
  },
  methods: {
    triggerFileInput() {
      this.$refs.fileInput.click();
    },
    triggerCoverInput() {
      this.$refs.coverInput.click();
    },
    handleDrop(e) {
      const file = e.dataTransfer.files[0];
      if (file && file.type.startsWith("video/")) {
        this.selectedFile = file;
        // Base64
        const reader = new FileReader();
        reader.onload = (e) => {
          this.videoUrl = e.target.result;
        };
        reader.readAsDataURL(file);
      }
    },
    handleFileSelect(e) {
      const file = e.target.files[0];
      if (file) {
        this.selectedFile = file;
        // Base64
        const reader = new FileReader();
        reader.onload = (e) => {
          this.videoUrl = e.target.result;
        };
        reader.readAsDataURL(file);
      }
    },
    handleCoverSelect(e) {
      const file = e.target.files[0];
      if (file) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.videoInfo.cover = e.target.result;
        };
        reader.readAsDataURL(file);
      }
    },
    removeCover() {
      this.videoInfo.cover = null;
    },
    addTag() {
      if (this.tagInput && this.videoInfo.tags.length < 10) {
        this.videoInfo.tags.push(this.tagInput);
        this.tagInput = "";
      }
    },
    removeTag(index) {
      this.videoInfo.tags.splice(index, 1);
    },
    resetUpload() {
      this.selectedFile = null;
      this.videoUrl = null;
      this.videoInfo = {
        title: "",
        category: "",
        description: "",
        tags: [],
        cover: null,
        publishType: "now",
        scheduleTime: null,
      };
    },
    submitVideo() {
      // 
      if (!this.videoInfo.title.trim()) {
        alert("");
        return;
      }
      if (!this.videoInfo.category) {
        alert("");
        return;
      }

      // 
      const newVideo = {
        id: Date.now(),
        title: this.videoInfo.title,
        views: "0",
        comments: "0",
        time: new Date().toLocaleString(),
        description: this.videoInfo.description,
        avatar: "http://113.45.69.13:9000/image/lucy_moon.jpg",
        video_url: this.videoUrl,
        image: this.videoInfo.cover || "http://113.45.69.13:9000/image/lucy_moon.jpg",
        show_right: false,
        author: "",
        follow: "0",
        is_like: false,
        is_dislike: false,
        is_collect: false,
        is_share: false,
        duration: "00:00",
        isCoined: false,
        like_count: 0,
        collect_count: 0,
        coin_count: 0,
        share_count: 0,
        category: this.videoInfo.category,
        tags: [...this.videoInfo.tags],
        publishType: this.videoInfo.publishType,
        scheduleTime: this.videoInfo.scheduleTime,
      };

      // videoInfos
      videoInfos.push(newVideo);

      // 
      alert("");
      this.resetUpload();

      // 
      this.$router.push("/");
      console.log(videoInfos);
    },
  },
};
</script>

<style scoped>
.upload-view {
  min-height: 100vh;
  background-color: #f4f4f4;
}

.upload-container {
  max-width: 1600px;
  margin: 0 auto;
  padding: 20px;
  display: flex;
  gap: 20px;
}

/*  */
.upload-main {
  flex: 1;
  background: white;
  border-radius: 8px;
  padding: 24px;
}

/*  */
.upload-area {
  height: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.upload-box {
  text-align: center;
  padding: 40px;
  border: 2px dashed #ddd;
  border-radius: 8px;
  background: #fafafa;
  width: 100%;
  max-width: 600px;
}

.upload-box i {
  font-size: 48px;
  color: #00aeec;
  margin-bottom: 16px;
}

.upload-box h3 {
  margin: 0 0 8px;
  font-size: 18px;
  color: #18191c;
}

.upload-box p {
  margin: 0 0 16px;
  color: #61666d;
}

.upload-limits {
  display: flex;
  justify-content: center;
  gap: 24px;
  margin-bottom: 24px;
  color: #61666d;
  font-size: 14px;
}

.file-input {
  display: none;
}

.select-btn {
  background: #00aeec;
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  transition: background 0.3s ease;
}

.select-btn:hover {
  background: #0095cc;
}

/*  */
.form-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
}

.form-header h2 {
  margin: 0;
  font-size: 20px;
}

.cancel-btn {
  background: none;
  border: none;
  color: #61666d;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 4px;
}

.form-section {
  margin-bottom: 32px;
}

.form-section h3 {
  font-size: 16px;
  margin: 0 0 16px;
}

.form-group {
  margin-bottom: 20px;
  position: relative;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  color: #18191c;
}

.required {
  color: #ff6b6b;
}

input[type="text"],
textarea,
select {
  width: 100%;
  padding: 8px 12px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 14px;
}

textarea {
  resize: vertical;
}

.char-count {
  position: absolute;
  right: 8px;
  bottom: 8px;
  color: #9499a0;
  font-size: 12px;
}

/*  */
.tags-input {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.tag {
  background: #f4f4f4;
  padding: 4px 8px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  gap: 4px;
  font-size: 14px;
}

.tag i {
  cursor: pointer;
}

.tags-input input {
  border: none;
  outline: none;
  padding: 4px;
  flex: 1;
  min-width: 100px;
}

/*  */
.cover-upload {
  width: 240px;
}

.cover-preview {
  position: relative;
  border-radius: 4px;
  overflow: hidden;
}

.cover-preview img {
  width: 100%;
  display: block;
}

.cover-actions {
  position: absolute;
  top: 8px;
  right: 8px;
}

.cover-actions button {
  background: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  cursor: pointer;
}

.cover-upload-box {
  border: 2px dashed #ddd;
  border-radius: 4px;
  padding: 24px;
  text-align: center;
  cursor: pointer;
}

.cover-upload-box i {
  font-size: 32px;
  color: #9499a0;
  margin-bottom: 8px;
}

/*  */
.radio-group {
  display: flex;
  gap: 24px;
  margin-bottom: 16px;
}

.radio {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}

.schedule-time {
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

/*  */
.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 16px;
  margin-top: 32px;
  padding-top: 24px;
  border-top: 1px solid #f4f4f4;
}

.draft-btn {
  padding: 12px 24px;
  border: 1px solid #ddd;
  background: white;
  border-radius: 4px;
  cursor: pointer;
}

.submit-btn {
  padding: 12px 24px;
  background: #00aeec;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.submit-btn:hover {
  background: #0095cc;
}
</style>
