  name: "WatchHistoryView",
  components: {
    VideoBar,
    HistoryTabs,
    HistoryCard,
    HistoryTimeline,
  },
  data() {
    return {
      recordHistory: true,
      search: "",
      activeTab: "",
      historyList: mockHistory,
    };
  },
  computed: {
    filteredHistory() {
      // activeTabsearch
      let list = this.historyList;
      if (this.activeTab !== "") {
        list = list.filter((item) => item.category === this.activeTab);
      }
      if (this.search) {
        list = list.filter(
          (item) => item.title.includes(this.search) || item.author.includes(this.search)
        );
      }
      return list;
    },
    groupedHistory() {
      // 
      return [
        { label: "" },
        // 
      ];
    },
  },
};
