export function parseDanmakuXml(xmlText) {
  try {
    // 
    if (xmlText.trim().startsWith('This XML file does not appear to have any style information associated with it. The document tree is shown below.')) {
      xmlText = xmlText.split('\n').slice(1).join('\n').trim();
    }

    const parser = new DOMParser();
    const xml = parser.parseFromString(xmlText, "text/xml");
    
    // id
    let dNodes = [];
    const root = xml.getElementsByTagName("i")[0];
    if (root) {
      dNodes = root.getElementsByTagName("d");
    } else {
      // id
      dNodes = xml.getElementsByTagName("d");
    }
    
    console.log(":", dNodes.length);
    
    const danmakus = [];

    // 
    const typeStats = {
      1: 0, // 
      4: 0, // 
      5: 0, // 
      7: 0, // 
      advanced: 0, // 
    };

    // 
    const trackStats = new Array(10).fill(0);

    for (let d of dNodes) {
      try {
        const pAttr = d.getAttribute("p");
        const content = d.textContent.trim();

        //  p 
        const p = pAttr.split(",");
        // 
        if (!p[0] || !p[1] || !p[2] || !p[3]) {
          console.warn(":", pAttr);
          continue;
        }

        const time = parseFloat(p[0]);
        const type = parseInt(p[1]); // 
        const fontSize = parseInt(p[2]);
        const color = parseColor(p[3]);
        const timestamp = parseInt(p[4]) || 0;
        const pool = parseInt(p[5]) || 0; // 
        const userHash = p[6] || "";
        const rowID = p[7] || "";
        const track = parseInt(p[8]) || 0; // 

        // 
        if (Object.prototype.hasOwnProperty.call(typeStats, type)) {
          typeStats[type]++;
        }

        // 
        if (type === 1) {
          // 
          trackStats[track]++;
        }

        // 
        let text = content;
        let position = null;
        let isAdvanced = false;

        if (content.startsWith("[") && content.endsWith("]")) {
          try {
            const jsonData = JSON.parse(content);
            isAdvanced = true;
            typeStats.advanced++;
            typeStats[7]++; // 7

            // 
            // [x,y,scale,duration,text,alpha,rotation,size,color,type,font,style,fontFamily,isAdvanced]
            text = jsonData[4] || content; // 

            // 
            const scaleMatch = (jsonData[2] || "1-1").match(/(\d+)-(\d+)/);
            const scaleX = scaleMatch ? parseFloat(scaleMatch[1]) : 1;
            const scaleY = scaleMatch ? parseFloat(scaleMatch[2]) : 1;

            position = {
              x: jsonData[0] || 0, // x
              y: jsonData[1] || 0, // y
              scaleX, // x
              scaleY, // y
              duration: jsonData[3] || 8, // 
              alpha: jsonData[5] || 1, // 
              rotation: jsonData[6] || 0, // 
              size: jsonData[7] || fontSize, // 
              color: jsonData[8] || color, // 
              type: jsonData[9] || type, // 
              font: jsonData[10] || "SimHei", // 
              style: jsonData[11] || 1, // 
              fontFamily: jsonData[12] || "SimHei", // 
              isAdvanced: jsonData[13] || 1, // 
            };

            console.log(":", {
              text,
              position,
              time,
              type,
              fontSize,
              color,
            });
          } catch (e) {
            console.warn(":", content, e);
            // 
            isAdvanced = false;
          }
        }

        // 
        if (!isAdvanced) {
          const trackHeight = 100 / 10; // 
          position = {
            x: type === 1 ? 100 : 50, // 
            y: type === 1 ? track * trackHeight + trackHeight / 2 : type === 5 ? 10 : 90, // XML
            duration: 8, // 
            font: "SimHei", // 
          };
        }

        const danmaku = {
          time,
          type,
          fontSize,
          color,
          timestamp,
          pool,
          userHash,
          rowID,
          track, // 
          text,
          position,
          isAdvanced,
          duration: position?.duration || 8,
          font: position?.font || "SimHei",
          isScrolling: type === 1 || type === 7, // 
        };

        // 
        if (isAdvanced) {
          console.log(":", {
            time,
            text,
            position,
            fontSize,
            color,
          });
        }

        danmakus.push(danmaku);
      } catch (e) {
        console.warn(":", e);
      }
    }

    // 
    console.log(":", typeStats);
    console.log(":", trackStats);
    console.log(":", danmakus.length);
    console.log(":", typeStats.advanced);
    console.log("7:", typeStats[7]);

    return danmakus;
  } catch (e) {
    console.error("XML:", e);
    return [];
  }
}

function parseColor(colorNum) {
  if (!colorNum || isNaN(colorNum)) return "#ffffff";

  const num = parseInt(colorNum);
  if (num > 0xffffff) {
    return `#${(num & 0xffffff).toString(16).padStart(6, "0")}`;
  }
  return `#${num.toString(16).padStart(6, "0")}`;
}
